import { useState, useRef } from "react";

import Input from "../input/Input";
import classes from "./MealItemForm.module.css";

// gets props from meal item (onAddToCart function)
// and sends back the entered amount in numbers
const MealItemForm = (props) => {
  const [isValidForm, setIsValidForm] = useState(true);
  const enterdAmountRef = useRef();

  const submitHandler = (event) => {
    event.preventDefault();

    const enterdAmount = enterdAmountRef.current.value;
    const enterdAmountNumber = +enterdAmount;

    if (
      enterdAmount.trim().length === 0 ||
      enterdAmountNumber < 1 ||
      enterdAmountNumber > 5
    ) {
      setIsValidForm(false);
      return;
    }
    props.onAddToCart(enterdAmountNumber);
  };

  return (
    <form className={classes.form} onSubmit={submitHandler}>
      <Input
        ref={enterdAmountRef}
        label="amount"
        input={{
          id: "amount_" + Math.random(),
          type: "number",
          min: "1",
          max: "5",
          defaultValue: "1",
        }}
      />
      <button>+ Add</button>
      {!isValidForm ? <p>The form is not valid</p> : null}
    </form>
  );
};

export default MealItemForm;
