import { useContext } from "react";

import MealItemForm from "../../form/MealItemForm";
import CartContext from "../../../store/cart-context";

import classes from "./MealItem.module.css";

// gets props from availableMeals
const MealItem = (props) => {
  const cartCtx = useContext(CartContext);
  // console.log("mealItem props.price", props.price.toFixed(2));

  const price = `$${props.price.toFixed(2)}`;
  // console.log("mealItem price", price);

  const addToCartHandler = (amount) => {
    cartCtx.addItem({
      id: props.id,
      name: props.name,
      amount: amount, // num of items in cart
      price: props.price,
    });
    // console.log("price in mealitem", props.price);
  };
  return (
    <li className={classes.meal}>
      <div>
        <h3>{props.name}</h3>
        <div className={classes.description}>{props.description}</div>
        <div className={classes.price}>{price}</div>
      </div>
      <div>
        <MealItemForm onAddToCart={addToCartHandler} />
      </div>
    </li>
  );
};

export default MealItem;
